





















































import Vue from "vue"
export default Vue.extend({
    name: "About",
    data: () => ({
        snackbar: false
    }),
    mounted() {
        const _this = this
        this.$store.state.messageListeners.add(function (ws: WebSocket, e: MessageEvent) {
            if (e.data == "completed"){
                _this.$data.snackbar = true
                setTimeout(() => _this.$data.snackbar = false, 3000)
            }
        })
    }
})
